// Initial template for a new scenario
const initialScenarioTemplate = {
    shares: 400,
    dividendAveragePerShare: 1.87,
    insurancePut: 900,
    timeInDays: 198,
    putStrike: 18,
    puts: 2,
    taxRate: 0.30,
    buyPrice: 21,
    sellPutCredit: 700,
    oneYrStockPriceEst: 17.86,
    dividendFrequency: 'monthly',
};


// Function to calculate all outputs for a given scenario
const calculateScenarioOutputs = (inputs) => {
    const shares = Number(inputs.shares) || 0;
    const dividendAveragePerShare = Number(inputs.dividendAveragePerShare) || 0;
    const insurancePut = Number(inputs.insurancePut) || 0;
    const timeInDays = Number(inputs.timeInDays) || 0;
    const putStrike = Number(inputs.putStrike) || 0;
    const puts = Number(inputs.puts) || 0;
    const taxRate = Number(inputs.taxRate) || 0;
    const buyPrice = Number(inputs.buyPrice) || 0;
    const sellPutCredit = Number(inputs.sellPutCredit) || 0;
    const oneYrStockPriceEst = Number(inputs.oneYrStockPriceEst) || 0;
    const dividendFrequency = inputs.dividendFrequency || 'monthly';

    const cost = shares * buyPrice;
    const dividendAnnual = shares * dividendAveragePerShare * (dividendFrequency === 'monthly' ? 12 : 4);
    const taxes = dividendAnnual * taxRate;
    const netDividendAnnual = dividendAnnual - taxes;
    const netPeriodicDiv = netDividendAnnual / (dividendFrequency === 'monthly' ? 12 : 4);
    const putSellCapitol = puts * 100 * putStrike;
    const noPutCapitol = Math.max(0, (shares - (puts * 100)) * (oneYrStockPriceEst));
    const capitalAfter1Year = putSellCapitol + noPutCapitol;
    const oneYearLossGain = cost - capitalAfter1Year;
    const capitolLoss = (oneYrStockPriceEst === 0 || oneYearLossGain > 0) ? oneYearLossGain : 0;
    const capitalGain = (oneYearLossGain < 0) ? Math.abs(oneYearLossGain) : 0;

    const totalAfter1Year = (dividendAnnual + sellPutCredit + capitalGain) - (capitolLoss + taxes + insurancePut);
    const gainLossPercentage = (cost + insurancePut) > 0 ? (totalAfter1Year / (cost + insurancePut)) * 100 : 0;

    let maxInsurancePutFor15Gain = 0;
    const numerator = (dividendAnnual + sellPutCredit + capitalGain) - (capitolLoss + taxes) - (0.15 * cost);
    const denominator = 1.15;
    if (denominator > 0) {
        const calculatedMax = numerator / denominator;
        if (!isNaN(calculatedMax) && calculatedMax > 0) {
            maxInsurancePutFor15Gain = calculatedMax;
        }
    }

    return {
        cost,
        dividendAnnual,
        taxes,
        netDividendAnnual,
        netPeriodicDiv,
        putSellCapitol,
        noPutCapitol,
        capitalAfter1Year,
        oneYearLossGain,
        capitolLoss,
        capitalGain,
        totalAfter1Year,
        gainLossPercentage,
        maxInsurancePutFor15Gain,
    };
};

function calculateCompoundGrowth(startingAmount, annualPercentage, annualContribution, numberOfYears) {
    const results = [];
    let currentAmount = startingAmount;
    let highlightedOnce = false;

    for (let year = 1; year <= numberOfYears; year++) {
        const growthAmount = currentAmount * (annualPercentage / 100);
        const amountBeforeContribution = currentAmount + growthAmount;
        const endingAmount = amountBeforeContribution + annualContribution;
















        const yearData = {
            year: year,
            startingAmount: parseFloat(currentAmount.toFixed(2)),
            growthAmount: parseFloat(growthAmount.toFixed(2)),
            annualContribution: parseFloat(annualContribution.toFixed(2)),
            endingAmount: parseFloat(endingAmount.toFixed(2))
        };
















        if (!highlightedOnce && endingAmount >= 1000000) {
            yearData.highlightStartingAmount = true;
            highlightedOnce = true;
        }
















        results.push(yearData);
        currentAmount = endingAmount;
    }
















    return results;
}






