    const getAiSummary = useCallback(async (data, isTsrSummary = false, isPortfolioSummary = false, showResultInModal = true) => {
        if (!userId) {
            if (showResultInModal) {
                setModalTitle('App Not Ready');
                setModalMessage('Please wait for the app to finish loading.');
                setShowModal(true);
            }
            return null;
        }

        setIsAiSummaryLoading(true);
        let prompt = '';
        let modalTitleText = 'AI Summary';

        if (isPortfolioSummary) {
            modalTitleText = 'AI Portfolio Analysis';
            prompt = `Act as a financial analyst and provide a brief, insightful summary (around 4-5 sentences) of the following dividend investment portfolio.
        
        Key Metrics:
        - Number of Stocks: ${data.numberOfStocks}
        - Tickers: ${data.tickers}
        - Total Amount Invested: ${formatCurrency(data.totalAmountInvested)}
        - Net Estimated Annual Income (after tax): ${formatCurrency(data.netAnnualIncome)}
        - Blended Tax Rate: ${formatPercentage(data.blendedTaxRate)}

        Your summary should touch upon:
        1. The relationship between the total investment and the net annual income.
        2. What the blended tax rate suggests about the portfolio's tax efficiency.
        3. Use all metrics in portfolio summary to build a fiscal narrative.
        4. A concluding sentence about the portfolio's concentration given the number of stocks.
        Do not give any financial advice, predict future performance, or use overly enthusiastic or promotional language.
        Maintain a neutral, analytical tone.`;
        } else if (isTsrSummary) {
            // ... (this part of the logic is unchanged)
        } else {
            // ... (this part of the logic is unchanged)
        }

        const chatHistory = [{ role: "user", parts: [{ text: prompt }] }];
        const payload = { contents: chatHistory };
        const apiUrl = `https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent?key=${geminiApiKey}`;

        try {
            const response = await fetch(apiUrl, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload)
            });
            const result = await response.json();

            if (result.candidates && result.candidates.length > 0 &&
                result.candidates[0].content && result.candidates[0].content.parts &&
                result.candidates[0].content.parts.length > 0) {
                const text = result.candidates[0].content.parts[0].text;
                if (showResultInModal) {
                    setModalTitle(modalTitleText);
                    setModalMessage(text);
                    setShowModal(true);
                }
                logApiCall('Gemini AI Summary', data.ticker || 'Portfolio', 'success', 'AI summary generated successfully.');
                return text;
            } else {
                const errorMsg = result.error?.message || 'Could not get AI summary. Unexpected response structure.';
                if (showResultInModal) {
                    setModalTitle('AI Summary Error');
                    setModalMessage(errorMsg);
                    setShowModal(true);
                }
                logApiCall('Gemini AI Summary', data.ticker || 'Portfolio', 'error', `Failed to generate: ${errorMsg}`);
            }
        } catch (error) {
            console.error("Error generating AI summary:", error);
            if (showResultInModal) {
                setModalTitle('AI Summary Error');
                setModalMessage(`Failed to generate AI summary: ${error.message}`);
                setShowModal(true);
            }
            logApiCall('Gemini AI Summary', data.ticker || 'Portfolio', 'error', `Fetch failed: ${error.message}`);
        } finally {
            setIsLoading(false);
            setIsAiSummaryLoading(false);
        }
        return null;
    }, [logApiCall, userId, geminiApiKey]); // MODIFIED: Added geminiApiKey to dependency array
















